import { Component, OnInit } from '@angular/core';
import { SettingsService } from '@user-interfaces/common';
import { HttpClient } from '@angular/common/http';
import { da } from 'date-fns/locale';
import { NgForm } from '@angular/forms';

@Component({
    selector: 'guest-component',
    templateUrl: './hostManage.component.html',
    styleUrls: ['./hostManage.component.css'],
})
export class HostManageComponent {
    public get background() {
        return this._settings.get('app.home.background');
    }

    public musers: Muser[];
    public staticUser: Muser[];

    email: any;
    p: number = 1;

    constructor(private _settings: SettingsService, public http: HttpClient) {}
    ngOnInit() {
        this.http
            .get<any>('http://localhost:3000/users/musers')
            .subscribe((data) => {
                this.musers = data.map((user) => {
                    return <Muser>user;
                });
                this.staticUser = this.musers;
                console.log(this.musers);
            });
    }

    udelete(user) {
        this.musers.splice(this.musers.indexOf(user));
        this.http
            .post<any>('http://localhost:3000/users/delete', {
                username: user.username,
            })
            .subscribe((data) => {
                console.log(data);
                return this.ngOnInit();
            });
    }

    // filter(hemail) {
    //     var email: string = hemail.value.email;
    //     this.musers = this.staticUser.filter((user) => {
    //         return user.username.startsWith(email);
    //     });
    // }

    // rest(hemail) {
    //     this.ngOnInit();
    //     hemail.reset();
    // }

    search() {
        if (this.email == '') {
            this.http
                .get<any>('http://localhost:3000/users/musers')
                .subscribe((data) => {
                    this.musers = data.map((user) => {
                        return <Muser>user;
                    });
                    this.staticUser = this.musers;
                    console.log(this.musers);
                });
        } else {
            this.musers = this.musers.filter((res) => {
                return res.username
                    .toLocaleLowerCase()
                    .match(this.email.toLocaleLowerCase());
            });
        }
    }
}

export interface Muser {
    firstName?: string;
    lastName?: string;
    username?: string;
    phonenumber?: string;
    approve?: boolean;
    password?: string;
}
