import { Component, OnInit } from '@angular/core';
import { SettingsService } from '@user-interfaces/common';
import { HttpClient } from '@angular/common/http';
import { da } from 'date-fns/locale';
import { NgForm } from '@angular/forms';

@Component({
    selector: 'guest-component',
    templateUrl: './hostApproval.component.html',
    styleUrls: ['./hostApproval.component.css'],
})
export class HostApproveComponent {
    public get background() {
        return this._settings.get('app.home.background');
    }

    public users: User[];

    constructor(private _settings: SettingsService, public http: HttpClient) {}
    ngOnInit() {
        this.http
            .get<any>('http://localhost:3000/users/all')
            .subscribe((data) => {
                this.users = data.map((users) => {
                    return <User>users;
                });
            });
    }

    reset() {
        this.users = null;
        this.http
            .get<any>('http://localhost:3000/users/all')
            .subscribe((data) => {
                this.users = data.map((users) => {
                    return <User>users;
                });
            });
    }

    approve(user) {
        console.log(user);
        this.http
            .post<any>('http://localhost:3000/users/approve', {
                username: user.username,
                firstName: user.firstName,
                lastName: user.lastName,
                phonenumber: user.phonenumber,
                password: user.password,
                approve: user.approve,
            })
            .subscribe((data) => {
                console.log(data);
                return this.ngOnInit();
            });
    }

    disapprove(user) {
        this.users.splice(this.users.indexOf(user));
        console.log(user);
        this.http
            .post<any>('http://localhost:3000/users/disapprove', {
                username: user.username,
                firstName: user.firstName,
                lastName: user.lastName,
                phonenumber: user.phonenumber,
                password: user.password,
                approve: user.approve,
            })
            .subscribe((data) => {
                console.log(data);
            });
    }
}
export interface User {
    firstName?: string;
    lastName?: string;
    username?: string;
    phonenumber?: string;
    approve?: boolean;
    password?: string;
}
